const Event = (props) => {
    const {name, url, description } = props.info;
    let desc = description.text;
    if(desc){
        desc = desc.substr(0,250);
    }
    return ( 
        <a 
            className="list-group-item 
                       active 
                       text-light 
                       mb-1"
            target="_blank"
            href={url}>
            <h3 
                className="mb-3">
                {name.text}
            </h3>
            <p
                className="mb-1">
                {desc}
            </p>
        </a>
     );
}
 
export default Event;